import { useSolitaire } from "./useSolitaire";
import Card from "./Card";
import "./style.css";
import Place from "./Place";
import { Flex } from "../../components";
import {
	DragAndDropProvider,
	Draggable,
	Droppable,
} from "../../react-dnd-indexes";
import { useCallback } from "react";

const columns = [0, 1, 2, 3, 4, 5, 6];
const queues = [0, 1, 2, 3];

export default function Solitaire(props) {
	const { toPlace } = useSolitaire();

	const dropHandler = useCallback(
		(e, state) => {
			const { draggable, droppableUnder, droppable } = state;

			if (!droppableUnder) {
				return;
			}

			if (droppableUnder === droppable) {
				return;
			}

			toPlace(draggable, droppableUnder);
		},
		[toPlace]
	);

	return (
		<DragAndDropProvider onDragEnd={dropHandler}>
			<Flex row justifyContent="center" style={{ padding: "10px" }}>
				<Place width={1110} height={750} style={{ position: "relative" }}>
					<Place x={0} y={0}>
						<Deck />
					</Place>

					<Place x={160} y={0}>
						<View />
					</Place>

					{queues.map((index) => (
						<Place key={index} x={480 + index * 160} y={0}>
							<Queue index={index} />
						</Place>
					))}

					{columns.map((index) => (
						<Place key={index} x={index * 160} y={230}>
							<Column index={index} />
						</Place>
					))}
				</Place>
			</Flex>
		</DragAndDropProvider>
	);
}

function Deck(props) {
	const { cards, deckFlip } = useSolitaire();

	const deck = cards
		.filter((card) => card.place === "deck")
		.sort((a, b) => a.index - b.index);

	if (!deck.length) {
		return <Place outlined onClick={deckFlip} />;
	}

	return deck.map((card) => (
		<Card key={card.name} {...card} onClick={() => deckFlip(card.name)} />
	));
}

function View(props) {
	const { cards } = useSolitaire();

	const view = cards
		.filter((card) => card.place === "view")
		.sort((a, b) => a.index - b.index);

	if (!view.length) {
		return null;
	}

	const viewElements = [];

	for (let i = 0; i < view.length - 1; i++) {
		const card = view[i];
		viewElements.push(<Card key={card.name} {...card} />);
	}

	const lastView = view[view.length - 1];
	viewElements.push(
		<Draggable key={lastView.name} index={lastView.name}>
			{({ ref, style }) => <Card ref={ref} {...lastView} style={style} />}
		</Draggable>
	);

	return viewElements;
}

function Column(props) {
	const { index } = props;
	const { cards, openCard } = useSolitaire();

	const column = cards
		.filter((card) => card.place === `column_${index}`)
		.sort((a, b) => a.index - b.index);

	if (!column.length) {
		return (
			<Droppable index={`column_${index}`}>
				{({ ref }) => <Place ref={ref} outlined />}
			</Droppable>
		);
	}

	const columnElements = [];

	for (let i = 0; i < column.length; i++) {
		const card = column[i];

		if (card.open) {
			columnElements.push(
				<ColumnItem
					key={card.name}
					cards={column.slice(i)}
					offset={i * 50}
					index={index}
				/>
			);
			break;
		}

		if (i === column.length - 1) {
			columnElements.push(
				<Card
					key={card.name}
					{...card}
					y={i * 50}
					onClick={() => openCard(card.name)}
				/>
			);
		} else {
			columnElements.push(<Card key={card.name} {...card} y={i * 50} />);
		}
	}

	return columnElements;
}

function ColumnItem(props) {
	const { cards, index, offset } = props;
	const card = cards[0];

	if (cards.length > 1) {
		return (
			<Draggable index={card.name}>
				{({ ref, style }) => (
					<Place
						ref={ref}
						style={{
							top: `${offset}px`,
							...style,
						}}
					>
						<Card {...card} />
						<ColumnItem offset={50} cards={cards.slice(1)} index={index} />
					</Place>
				)}
			</Draggable>
		);
	}

	return (
		<Droppable index={`column_${index}`}>
			{({ ref }) => (
				<Place ref={ref} style={{ top: `${offset}px` }}>
					<Draggable index={card.name}>
						{({ ref, style }) => <Card ref={ref} {...card} style={style} />}
					</Draggable>
				</Place>
			)}
		</Droppable>
	);
}

function Queue(props) {
	const { index } = props;
	const { cards } = useSolitaire();

	const queue = cards
		.filter((card) => card.place === `queue_${index}`)
		.sort((a, b) => a.index - b.index);

	if (!queue.length) {
		return (
			<Droppable index={`queue_${index}`}>
				{({ ref }) => <Place ref={ref} outlined />}
			</Droppable>
		);
	}

	const queueElements = [];
	for (let i = 0; i < queue.length - 1; i++) {
		const card = queue[i];
		queueElements.push(<Card key={card.name} {...card} />);
	}

	const lastCard = queue[queue.length - 1];
	queueElements.push(
		<Droppable key={lastCard.name} index={`queue_${index}`}>
			{({ ref }) => (
				<Place ref={ref}>
					<Draggable index={lastCard.name}>
						{({ ref, style }) => <Card ref={ref} style={style} {...lastCard} />}
					</Draggable>
				</Place>
			)}
		</Droppable>
	);

	return queueElements;
}
