import { useCallback, useState } from "react";

export default function useThrottle(callback, timeout = 100) {
	const [lastCallTime, setLastCallTime] = useState(0);

	return useCallback(
		(...args) => {
			const now = Date.now();

			if (now > lastCallTime + timeout) {
				setLastCallTime(now);
				return callback(...args);
			}
		},
		[lastCallTime, timeout, callback]
	);
}
