import { createRef, useEffect, useMemo, useState } from "react";
import PropTypes from "prop-types";
import useDragAndDrop from "./useDragAndDrop";
import useDndItems from "./useDndItems";

export default function useDrag(props) {
	const { index } = props;
	const innerRef = props?.ref;

	const ref = useMemo(() => (innerRef ? innerRef : createRef()), [innerRef]);
	const { current } = ref;

	const { draggable, mouseEvent } = useDragAndDrop();
	const { addDndItem, removeDndItem } = useDndItems();

	const [dragged, setDragged] = useState(false);
	const [offsetX, setOffsetX] = useState(0);
	const [offsetY, setOffsetY] = useState(0);

	const clientX = mouseEvent?.clientX;
	const clientY = mouseEvent?.clientY;

	useEffect(() => {
		if (index === draggable && current && mouseEvent && !dragged) {
			const rect = current.getBoundingClientRect();
			const { left, top } = rect;

			setDragged(true);
			setOffsetX(clientX - left);
			setOffsetY(clientY - top);
		}
	}, [clientX, clientY, index, draggable, current, mouseEvent, dragged]);

	useEffect(() => {
		if (index !== draggable && dragged) {
			setDragged(false);
		}
	}, [index, draggable, dragged]);

	useEffect(() => {
		if (current) {
			addDndItem({
				index,
				element: current,
				draggable: true,
			});

			return () => {
				removeDndItem({
					index,
					draggable: false,
				});
			};
		}
	}, [current, addDndItem, removeDndItem, index]);

	const style = useMemo(() => {
		if (!dragged) {
			return {};
		}

		return {
			zIndex: 10,
			margin: 0,
			position: "fixed",
			left: `${clientX - offsetX}px`,
			top: `${clientY - offsetY}px`,
		};
	}, [dragged, clientX, clientY, offsetX, offsetY]);

	return {
		ref,
		dragged,
		style,
	};
}

useDrag.propTypes = {
	index: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
};
