import { useEffect, useState } from "react";
import PropTypes from "prop-types";

import { MOUSE_DOWN, MOUSE_MOVE, MOUSE_UP } from "./additional";
import useElementsUnderPoint from "./useElementsUnderPoint";
import useDndItems, { DndItemsProvider } from "./useDndItems";
import useThrottle from "./useThrottle";
import useQueue from "./useQueue";
import useMouse from "./useMouse";

import Context from "./Context";

function DragAndDropProvider(props) {
	const { children, onDragStart, onDragUpdate, onDragEnd } = props;

	const [state, setState] = useState({
		draggable: null,
		droppable: null,

		draggableUnder: null,
		droppableUnder: null,
	});

	const mouse = useMouse();
	const { mouseType, prevMouseType, mouseEvent } = mouse;

	const { dndItems } = useDndItems();
	const elements = useElementsUnderPoint(mouseEvent);
	const queue = useQueue(elements, dndItems, state.draggable);

	const onDragUpdateThrottle = useThrottle(onDragUpdate, 100);

	useEffect(() => {
		setState((state) => {
			const newState = { ...state };

			const draggableUnder = queue.find(
				(item) => item.draggable && state.draggable !== item.index
			);

			const droppableUnder = queue.find(
				(item) => item.droppable && state.draggable !== item.index
			);

			newState.draggableUnder = draggableUnder ? draggableUnder.index : null;
			newState.droppableUnder = droppableUnder ? droppableUnder.index : null;

			return newState;
		});
	}, [queue]);

	useEffect(() => {
		if (mouseType === MOUSE_DOWN) {
			setState((state) => {
				if (state.draggable || !state.draggableUnder) {
					return state;
				}

				const newState = {
					...state,
					draggable: state.draggableUnder,
					droppable: state.droppableUnder,
				};

				if (newState.droppable === newState.draggable) {
					const index = queue.findIndex(
						(item) => item.index === newState.droppable
					);

					for (let i = index + 1; i < queue.length; i++) {
						const item = queue[i];

						if (item.droppable) {
							newState.droppable = item.index;
						}
					}
				}

				return newState;
			});
		}

		if (mouseType === MOUSE_MOVE && state.draggable) {
			if (prevMouseType === MOUSE_DOWN) {
				onDragStart(mouseEvent, {
					...state,
					queue,
				});
			}

			if (state.draggable) {
				onDragUpdateThrottle(mouseEvent, {
					...state,
					queue,
				});
			}

			return;
		}

		if (mouseType === MOUSE_UP) {
			if (state.draggable) {
				onDragEnd(mouseEvent, {
					...state,
					queue,
				});

				setState({
					...state,
					draggable: null,
					droppable: null,
				});
			}
		}
	}, [
		queue,
		state,
		mouseType,
		prevMouseType,
		mouseEvent,
		onDragStart,
		onDragUpdateThrottle,
		onDragEnd,
	]);

	return (
		<Context.Provider
			value={{
				...state,
				...mouse,
				queue,
			}}
		>
			{children}
		</Context.Provider>
	);
}

const Default = ({ children, ...props }) => (
	<DndItemsProvider>
		<DragAndDropProvider {...props}>{children}</DragAndDropProvider>
	</DndItemsProvider>
);

export default Default;

DragAndDropProvider.propTypes = {
	onDragStart: PropTypes.func.isRequired,
	onDragUpdate: PropTypes.func.isRequired,
	onDragEnd: PropTypes.func.isRequired,
};

DragAndDropProvider.defaultProps = {
	onDragStart() {},
	onDragUpdate() {},
	onDragEnd() {},
};
