import PropTypes from "prop-types";
import { forwardRef, useMemo } from "react";

const Place = forwardRef((props, ref) => {
	const {
		x,
		y,
		outlined,
		width,
		height,
		style = {},
		onClick,
		children,
	} = props;

	const ownStyle = useMemo(() => {
		const ownStyle = {
			position: "absolute",
			left: `${x}px`,
			top: `${y}px`,
			width: `${width}px`,
			height: `${height}px`,
		};

		if (outlined) {
			ownStyle.border = "1px dashed white";
		}

		return {
			...ownStyle,
			...style,
		};
	}, [x, y, outlined, width, height, style]);

	return (
		<div ref={ref} style={ownStyle} onClick={onClick}>
			{children}
		</div>
	);
});

export default Place;

Place.displayName = "Place";

Place.propTypes = {
	outlined: PropTypes.bool.isRequired,
	x: PropTypes.number.isRequired,
	y: PropTypes.number.isRequired,
	width: PropTypes.number.isRequired,
	height: PropTypes.number.isRequired,
	onClick: PropTypes.func.isRequired,
};

Place.defaultProps = {
	outlined: false,
	x: 0,
	y: 0,
	width: 150,
	height: 220,
	onClick() {},
};
