import { createContext, useCallback, useContext, useState } from "react";

const SolitaireContext = createContext();

function SolitaireProvider(props) {
	const { children } = props;

	const [cards, setCards] = useState(getCards());

	const deckFlip = useCallback(() => {
		const deck = cards
			.filter((card) => card.place === "deck")
			.sort((a, b) => a.index - b.index);

		const view = cards
			.filter((card) => card.place === "view")
			.sort((a, b) => a.index - b.index);

		if (deck.length) {
			const card = deck[deck.length - 1];
			Object.assign(card, { place: "view", index: view.length, open: true });
		} else {
			view.forEach((card, i) =>
				Object.assign(card, {
					place: "deck",
					index: view.length - i - 1,
					open: false,
				})
			);
		}

		return setCards(getCopy(cards));
	}, [cards]);

	const toPlace = useCallback(
		(name, place) => {
			const card = cards.find((card) => card.name === name);

			const source = cards
				.filter((x) => x.place === card.place)
				.sort((a, b) => a.index - b.index);

			const destination = cards
				.filter((card) => card.place === place)
				.sort((a, b) => a.index - b.index);

			if (place.includes("queue")) {
				if (source[source.length - 1] !== card) {
					return;
				}

				let replaceValid = false;

				if (destination.length) {
					const lastDestinationCard = destination[destination.length - 1];
					replaceValid = isValidOrder(lastDestinationCard, card, true);
				} else {
					replaceValid = card.value === "ace";
				}

				if (replaceValid) {
					card.place = place;
					card.index = destination.length;
					console.log(destination.length);
				}
			} else if (place.includes("column")) {
				let replaceValid = !destination.length;

				if (destination.length) {
					const lastDestinationCard = destination[destination.length - 1];

					replaceValid =
						isValidOrder(card, lastDestinationCard) &&
						!isSameColor(card, lastDestinationCard);
				}

				if (replaceValid) {
					const sourceIndex = source.indexOf(card);

					for (let i = sourceIndex; i < source.length; i++) {
						Object.assign(source[i], {
							place,
							index: destination.length + i - sourceIndex,
						});
					}
				}
			}

			setCards(getCopy(cards));
		},
		[cards]
	);

	const openCard = useCallback(
		(name) => {
			const card = cards.find((card) => card.name === name);
			card.open = true;

			setCards(getCopy(cards));
		},
		[cards]
	);

	return (
		<SolitaireContext.Provider value={{ cards, deckFlip, toPlace, openCard }}>
			{children}
		</SolitaireContext.Provider>
	);
}

const getCopy = (x) => JSON.parse(JSON.stringify(x));

function useSolitaire() {
	return useContext(SolitaireContext);
}

export { useSolitaire, SolitaireProvider };
export default useSolitaire;

const suits = "clubs diamonds hearts spades".split(" ");
const values = "ace 2 3 4 5 6 7 8 9 10 jack queen king".split(" ");

const isValidOrder = (card1, card2, withSuit = false) => {
	if (withSuit && card1.suit !== card2.suit) {
		return false;
	}

	const card1ValueIndex = values.indexOf(card1.value);
	const card2ValueIndex = values.indexOf(card2.value);

	return card2ValueIndex - card1ValueIndex === 1;
};

const redSuits = ["diamonds", "hearts"];
const isSameColor = (...cards) => {
	const isReds = cards.map((card) => redSuits.includes(card.suit));
	return isReds.every((value) => value === isReds[0]);
};

const getCards = () => {
	const cards = shuffle(getDeck());

	let counter = 0;
	for (let column = 0; column < 7; column++) {
		for (let index = 0; index <= column; index++) {
			Object.assign(cards[counter], {
				place: `column_${column}`,
				index,
				open: index === column,
			});
			counter++;
		}
	}

	cards
		.filter((card) => card.place === "deck")
		.forEach((card, index) => (card.index = index));

	return cards;
};

const getDeck = () => {
	const cards = [];

	for (const suit of suits) {
		for (const value of values) {
			cards.push({
				suit,
				value,
				open: false,
				name: `${value}_of_${suit}`,
				place: "deck",
				index: 0,
			});
		}
	}

	return cards;
};

const shuffle = (xs) => {
	const array = xs.splice(0);

	while (array.length) {
		const index = Math.floor(Math.random() * array.length);
		xs.push(array.splice(index, 1)[0]);
	}

	return xs;
};
