export const MOUSE_DOWN = "down";
export const MOUSE_MOVE = "move";
export const MOUSE_UP = "up";
export const MOUSE_LEFT = 1;

const borrow =
	(type, method) =>
	(x, ...args) =>
		type.prototype[method].call(x, ...args);

export const indexOf = borrow(Array, "indexOf");
export const includes = borrow(Array, "includes");
