import { useState } from "react";

export default function useElementsUnderPoint(mouseEvent) {
	const [elements, setElements] = useState([]);

	const clientX = mouseEvent?.clientX ?? -1;
	const clientY = mouseEvent?.clientY ?? -1;

	const elementsUnder = document.elementsFromPoint(clientX, clientY);

	if (elementsUnder.length !== elements.length) {
		setElements(elementsUnder);
		return elementsUnder;
	}

	for (let i = 0; i < elements.length; i++) {
		if (elementsUnder[i] !== elements[i]) {
			setElements(elementsUnder);
			return elementsUnder;
		}
	}

	return elements;
}
