import PropTypes from "prop-types";
import { useMemo } from "react";

export default function Flex(props) {
	const { column, row, wrap, justifyContent, children, style = {} } = props;

	const currentStyle = useMemo(() => {
		const currentStyle = {
			display: "flex",
			justifyContent,
			flexWrap: wrap,
			...style,
		};

		if (column) {
			currentStyle.flexDirection = "column";
		} else if (row) {
			currentStyle.flexDirection = "row";
		}

		return currentStyle;
	}, [column, row, justifyContent, style, wrap]);

	return <div style={currentStyle}>{children}</div>;
}

Flex.propTypes = {
	column: PropTypes.bool.isRequired,
	row: PropTypes.bool.isRequired,

	wrap: PropTypes.oneOf([
		"nowrap",
		"wrap",
		"wrap-reverse",
		"inherit",
		"initial",
		"unset",
	]).isRequired,

	justifyContent: PropTypes.oneOf([
		"center",
		"start",
		"end",
		"flex-start",
		"flex-end",
		"left",
		"right",
		"baseline",
		"first baseline",
		"last baseline",
		"space-between;",
		"space-around;",
		"space-evenly",
		"stretch",
		"safe center",
		"unsafe center",
		"inherit",
		"initial",
		"unset",
	]).isRequired,
};

Flex.defaultProps = {
	column: false,
	row: false,
	wrap: "wrap",
	justifyContent: "start",
};
