import { createContext, useCallback, useContext, useState } from "react";

const Context = createContext();

export const DndItemsProvider = (props) => {
	const { children } = props;

	const [dndItems, setDndItems] = useState([]);

	const addDndItem = useCallback(
		(dndItem) =>
			setDndItems((dndItems) => {
				let added = false;

				for (let i = 0; i < dndItems.length; i++) {
					if (dndItems[i].index === dndItem.index) {
						dndItems[i] = {
							...dndItems[i],
							...dndItem,
						};

						added = true;
						break;
					}
				}

				if (!added) {
					dndItems.push({
						draggable: false,
						droppable: false,
						...dndItem,
					});
				}

				return [...dndItems];
			}),
		[]
	);

	const removeDndItem = useCallback(
		(dndItem) =>
			setDndItems((dndItems) => {
				let removed = false;

				for (let i = 0; i < dndItems.length; i++) {
					if (dndItems[i].index === dndItem.index) {
						dndItems[i] = {
							...dndItems[i],
							...dndItem,
						};
						removed = true;
					}
				}

				if (removed) {
					dndItems = dndItems.filter(
						(dndItem) => dndItem.draggable || dndItem.droppable
					);
				}

				return dndItems;
			}),
		[]
	);

	return (
		<Context.Provider value={{ dndItems, addDndItem, removeDndItem }}>
			{children}
		</Context.Provider>
	);
};

export default function useDndItems() {
	return useContext(Context);
}
