import { createRef, useEffect, useMemo } from "react";
import PropTypes from "prop-types";
import useDragAndDrop from "./useDragAndDrop";
import useDndItems from "./useDndItems";

export default function useDrop(props) {
	const { index } = props;
	const innerRef = props?.ref;

	const ref = useMemo(() => (innerRef ? innerRef : createRef()), [innerRef]);
	const { current } = ref;

	const { draggable } = useDragAndDrop();
	const { addDndItem, removeDndItem } = useDndItems();

	const dragOver = useMemo(() => index === draggable, [index, draggable]);

	useEffect(() => {
		if (current) {
			addDndItem({
				index,
				element: current,
				droppable: true,
			});

			return () => {
				removeDndItem({
					index,
					droppable: false,
				});
			};
		}
	}, [current, addDndItem, removeDndItem, index]);

	return {
		dragOver,
		ref,
	};
}

useDrop.propTypes = {
	index: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
};
