import PropTypes from "prop-types";
import { useEffect, useState } from "react";
import { includes, indexOf } from "./additional";

export default function useQueue(elements, dndItems, draggable) {
	const [queue, setQueue] = useState([]);

	useEffect(() => {
		const queue = dndItems
			.filter(
				({ element, index }) =>
					includes(elements, element) && index !== draggable
			)
			.sort((a, b) => {
				const indexA = indexOf(elements, a.element);
				const indexB = indexOf(elements, b.element);

				return indexA - indexB;
			});

		if (draggable) {
			const draggableDndItem = dndItems.find(
				(dndItem) => dndItem.index === draggable
			);

			if (draggableDndItem) {
				queue.unshift(draggableDndItem);
			}
		}

		setQueue(queue);
	}, [elements, dndItems, draggable]);

	return queue;
}

useQueue.propTypes = {
	draggable: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	elements: PropTypes.array.isRequired,
	dndItems: PropTypes.array.isRequired,
};
